<template>
  <div class="zd-common-component">
    <div class="zd-common-clearFix">
      <div class="zd-common-fr">
        <DatePicker :editable="false" type="daterange" :options="dateOptions" placeholder="日期"
                    class="zd-common-input-h38 zd-common-fl zd-common-mr10 zd-cm-w220" @on-change="changeDate"></DatePicker>
        <Select v-model="type" placeholder="全部单据类型" class="zd-common-input-h38 zd-common-w150 zd-common-fl zd-common-mr10" clearable>
          <Option v-for="item in typeArr" :value="item[0]" :key="item[0]">{{ item[1] }}</Option>
        </Select>
        <Input v-model="orderNO" placeholder="单号" :maxlength="50" class="zd-common-input-h38 zd-common-w150 zd-common-fl zd-common-mr10"/>
        <Button type="primary" class="zd-common-fl" @click="selectHandle" :loading="selectLoading">
          <span v-if="!selectLoading">查询</span>
          <span v-else>正在查询...</span>
        </Button>
      </div>
    </div>
    <div class="zd-common-mt16">
      <Table ref="currentRowTable" border :columns="columns" :data="data"></Table>
      <div class="zd-common-tc zd-common-mt20">
        <Page :total="total" :current="pageNum" :page-size="pageSize" @on-change="changePage" @on-page-size-change="changePageSize" show-sizer show-elevator show-total></Page>
      </div>
    </div>
    <!--恢复-->
    <Modal v-model="recoveryModal" :mask-closable="false" :closable="false" :width="360">
      <h3 slot="header"></h3>
      <div class="zd-common-tc">
        <div class="zd-common-modal-waring zd-common-auto"></div>
        <p class="zd-common-primary-c zd-common-font-size-large zd-common-mt15">是否确认恢复？</p>
      </div>
      <div slot="footer" class="zd-common-modal-footer zd-common-pt10">
        <Button type="primary" class="zd-common-mr35 zd-common-w100 zd-common-h32 zd-common-lh8" @click="confirmRecovery"
                :loading="saveLoading">
          <span v-if="!saveLoading">确定</span>
          <span v-else>正在保存...</span>
        </Button>
        <Button @click="recoveryModal = false" class="zd-common-w100 zd-common-h32 zd-common-lh8">取消</Button>
      </div>
    </Modal>

  </div>
</template>
<script>
  export default {
    data() {
      return {
        //列表
        columns: [
          {
            type: 'index',
            align: 'center',
            title: '序号',
            width:65
          },
          {
            title: '单据类型',
            align: 'center',
            key: 'orderType'
          },
          {
            title: '单号',
            align: 'center',
            key: 'orderNO',
            width:140
          },
          {
            title: '操作人',
            align: 'center',
            key: 'actionPerson',
          },
          {
            title: '操作日期',
            align: 'center',
            key: 'actionDate',
          },
          {
            title: '原因',
            align: 'center',
            key: 'reason',
          },
          {
            title: '操作',
            align: 'center',
            key: 'action',
            width:180,
            render: (h, params) => {
              return h('div', [
                h('Button', {
                  props:{
                    type:'primary',
                    ghost:true,
                  },
                  style: {
                    height: "28px",
                    padding:'0 15px 1px',
                  },
                  on: {
                    click: () => {
                      this.showRecoveryModal(params.row.id);
                    }
                  }
                }, '恢复'),
              ]);
            }
          }
        ],
        data: [],
        pageSize:10,
        pageNum:1,
        total:0,
        //恢复
        rId:'',
        recoveryModal:false,
        //查询
        dateOptions: this.dateRange,
        startDate:'',
        endDate:'',
        typeArr:[],
        type:'',
        orderNO:'',
        selectLoading:false,
        saveLoading:false,
      }
    },
    created(){
      this.getRecoveryKind();
      this.getRecoveryRecordPage(this.pageNum,this.pageSize,this.startDate,this.endDate,this.type,this.orderNO);
    },
    methods: {
      //日期
      changeDate(arr){
        this.startDate = arr[0];
        this.endDate = arr[1];
      },
      //获取单据类型
      getRecoveryKind() {
        BaseAxios({
          method: 'post',
          url: '/sr/storeroomData!getRecoveryKind.action',
          data:{},
        })
          .then((response) => {
            let aData = response.data;
            this.typeArr = Object.entries(aData)
          })
          .catch((error) => {
            this.postErrorTip('请求失败');
            console.log(error);
          });
      },
      //恢复
      showRecoveryModal(id){
        this.rId = id;
        this.recoveryModal = true;
      },
      confirmRecovery(){
        this.saveLoading = true;
        this.recoveryRecord(this.rId);
      },
      recoveryRecord(id) {
        BaseAxios({
          method: 'post',
          url: '/sr/storeroomData!recoveryRecord.action',
          data:{
            id:id
          },
        })
          .then((response) => {
            let aData = response.data;
            if(aData!==""){
              this.postWarningTip(aData);
            }else{
              this.postSuccessTip('恢复成功');
            }
            this.saveLoading = false;
            this.recoveryModal = false;
            this.getRecoveryRecordPage(this.pageNum,this.pageSize,this.startDate,this.endDate,this.type,this.orderNO);
          })
          .catch((error) => {
            this.saveLoading = false;
            this.recoveryModal = false;
            this.postErrorTip('请求失败');
            console.log(error);
          });
      },
      //列表
      getRecoveryRecordPage(pageNum,pageSize,startDate,endDate,kind,num) {
        this.data = [];
        BaseAxios({
          method: 'post',
          url: '/sr/storeroomData!getRecoveryRecordPage.action',
          data:{
            pageNum:pageNum,
            pageSize:pageSize,
            startDate:startDate,
            endDate:endDate,
            kind:kind,
            num:num,
          },
        })
          .then((response) => {
            let aData = response.data;
            if(aData && aData.list.length>0){
              aData.list.map((item)=>{
                this.data.push({
                  id:item.id,
                  orderType:item.kind,
                  orderNO:item.num,
                  actionPerson:item.userName,
                  actionDate:item.date,
                  reason:item.cause,
                })
              })
            }
            this.total = aData.totalCount;
            this.selectLoading = false;
          })
          .catch((error) => {
            this.selectLoading = false;
            this.postErrorTip('请求失败');
            console.log(error);
          });
      },
      //分页
      changePage(num) {
        this.pageNum = num;
        this.getRecoveryRecordPage(this.pageNum,this.pageSize,this.startDate,this.endDate,this.type,this.orderNO);
      },
      changePageSize(num) {
        this.pageSize = num;
        this.getRecoveryRecordPage(this.pageNum,this.pageSize,this.startDate,this.endDate,this.type,this.orderNO);
      },
      //查询
      selectHandle(){
        this.selectLoading = true;
        this.getRecoveryRecordPage(this.pageNum,this.pageSize,this.startDate,this.endDate,this.type,this.orderNO);
      },
    },
    mounted() {

    }
  }
</script>
<style lang="less" scoped>
  .ivu-table .demo-table-info-row td{
    background-color: rgba(28, 144, 243, 0.1);
  }
</style>



